@use "../abstracts/variables";
@use "../abstracts/mixins";

.calendly {
  &__button {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
  }

  &__modal-iframe {
    display: block !important;
    height: 0 !important;
    background-color: transparent;

    @include mixins.media('desktop') {
      width: 100%;
      max-width: 900px;
    }
  }

  &__modal.modal {
    padding: 15px 5px 0;
    max-height: 96%;
    width: 97%;

    @include mixins.mediaCustom('min-width: 440px') {
      padding: 15px 15px 0 0;
      width: 80%;
    }

    @include mixins.media('desktop') {
      height: auto;
      padding: 0;
      width: 900px;
      max-height: 98%;
      max-width: 900px;
    }
  }

  .calendly__modal-iframe .DayPicker {
    width: 100% !important;
  }

  &__modal[data-active="true"] .calendly__modal-iframe  {
    width: 100%;
    height: 580px !important;

    // @include mixins.mediaCustom('min-width: 440px') {
    // }

    @include mixins.media('desktop-s') {
      height: 600px !important;
    }

    @include mixins.media('desktop-m') {
      height: 650px !important;
    }

    .items-center {
      padding: 0 !important;
    }
  }

  .calendly__modal-close-btn {
    position: absolute;
    top: 6px;
    right: 0px;
    padding: 6px;
    background: variables.$color-white;
    border-radius: 100%;
    line-height: 0;
    @include mixins.clickEffect;

    @include mixins.mediaCustom('min-width: 440px') {
      top: 6px;
      right: 8px;
    }

    @include mixins.mediaCustom('min-width: 975px') {
      right: 31px;
      top: 31px;
      padding: 15px;
      background: unset;
    }

    svg {
      height: 9px;
      width: 9px;

      @include mixins.mediaCustom('min-width: 975px') {
        height: 13px;
        width: 13px;
      }
    }
  }
}
