@use '../abstracts/variables';
@use '../abstracts/mixins';

.auth {
  width: 100%;
  padding: variables.$margin-m;
  text-align: center;

  & > * {
    display: block;

    &[data-active="false"] {
      display: none;
    }
  }

  &__buttons {
    --direction: column;
    margin-top: 34px;

    &-wrapper,.btn-1 {
      width: 100%;
    }

    &-wrapper a {
      text-transform: uppercase;
    }

    &-wrapper a:visited {
      color: variables.$color-principal;
    }

    & > *:first-of-type {
      margin: 0 0 variables.$margin-xs;
      font-size: variables.$font-size--general-s;
      line-height: variables.$line-height--general-up-s;
    }

    & > *:nth-of-type(2), &-wrapper:nth-of-type(3) {
      margin: 0 0 10px !important;
      margin-right: 0 !important;
      font-size: variables.$font-size--general-up-s;
      line-height: variables.$line-height--general-up-s;
    }

    a {
      @include mixins.linkEffect(currentColor, 1px, false);
    }
  }

  &__login {
    display: flex;
    flex-direction: column;
  }

  &__register {
    margin-top: 40px !important;

    @include mixins.media('desktop') {
      margin-top: 90px !important;
    }

    .loading {
      border-top-color: variables.$color-white;
    }
  }

  &__recover[data-active="false"] {
    display: block;
    order: -1;

    h2,
    .h2,
    p,
    input,
    .form__textfield ,
    .auth__buttons {
      display: none;
    }
  }

  &__recover #recover {
    text-transform: uppercase;
  }

  &.reset-password form {
    margin-top: variables.$margin-l;
  }

  #password-error,
  #password_confirmation-error {
    margin-top: 10px;
  }
}
