@use '../abstracts/variables';
@use '../abstracts/mixins';

$max-break-customer: 'max-width: 991px';
$margin-customer: 50px;

.customer {
  position: relative;
  margin: 100px auto;
  max-width: 528px;
  padding: 0 variables.$margin-m;
  width: 100%;

  @include mixins.media('desktop') {
    margin: 182px auto 175px;
    padding: 0;
  }

  .title, &__title {
    color: variables.$color-black;
    font-size: variables.$font-size--mobile--h1-customer;
    line-height: variables.$line-height--mobile--h1-customer;

    @include mixins.media('tablet-l') {
      font-size: variables.$font-size--mobile-h1;
      line-height: variables.$line-height--mobile-h1;
    }
  }

  a:visited {
    color: variables.$color-principal;
  }

  &:has(.customer__layout) {
    margin: 40px 0;
    max-width: none;
    padding: 0;

    @include mixins.media('desktop') {
      margin: variables.$margin-section 0;
      padding-left: 164px;
      padding-right: 164px;
    }
  }

  .form__textfield-notlabel:has(.form__password) {
    position: relative;
    cursor: pointer;
  }

  .form__password {
    position: absolute;
    top: 50%;
    right: 12px;
    height: 22px;
    transform: translateY(-50%);

    &[data-active="false"] {
      svg:last-child {
        display: none;
      }
    }
    &[data-active="true"] {
      svg:first-child {
        display: none;
      }
    }
  }

  form {
    .button {
      width: 100%;
    }

    a, button {
      font-size: variables.$font-size--general-m;
      line-height: variables.$line-height--general-up-s;
    }

    #consent {
      flex-direction: row;
      position: relative;
      margin-top: variables.$margin-m !important;
      margin-bottom: variables.$margin-l !important;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      color: variables.$color-light-black;

      input, label {
        display: inline;
        width: auto;
      }

      input {
        position: absolute;
        width: 0;
        height: 0;
        opacity: 0;
        cursor: pointer;
      }

      label {
        margin-left: 26px;
        font-size: variables.$font-size--general-m;
        line-height: variables.$line-height--general-m;
        text-align: start;
        cursor: pointer;
      }

      .checkmark {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: 16px;
        height: 16px;
        background-color: variables.$color-white;
        border: 1px solid variables.$color-background-4;
      }

      .checkmark:after {
        content: "";
        position: absolute;
        display: none;
      }
    }

    #consent:hover input ~ .checkmark {
      background-color: variables.$color-background-5;
    }

    #consent input:checked ~ .checkmark {
      background-color: variables.$color-principal;
    }

    #consent input:checked ~ .checkmark:after {
      display: block;
    }

    /* Style the checkmark/indicator */
    #consent .checkmark:after {
      left: 5px;
      top: 2px;
      width: 3px;
      height: 6px;
      border: solid variables.$color-white;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    }
  }

  &__title {
    text-transform: uppercase;
    text-align: center;
    margin-bottom: variables.$margin-m;

    @include mixins.media('tablet-l') {
      margin-bottom: variables.$margin-l;
    }
  }

  .errors ul li a {
    color: variables.$color-error;
  }

  #customer_login {
    #display-recover {
      display: inline-block;
      width: 100%;
      margin: 10px 0 0;
      text-transform: uppercase;
      text-align: start;
      color: variables.$color-principal;
    }

    a:hover {
      opacity: .75;
    }

    .form__message--error {
      font-size: variables.$font-size--general-m;
    }
  }

  #create_customer, #create_customer .auth__buttons {
    margin-top: variables.$margin-l;
  }

  #recover-container {
    form, .auth__buttons {
      margin-top: variables.$margin-l;
    }
    form .auth__buttons div:first-child,
    form .btn-1 {
      width: 100%;
    }
    #recover-hidden {
      text-transform: uppercase;
    }
  }

  & > &__nav-link {
    text-align: center;
    text-transform: uppercase;
    margin-bottom: variables.$margin-section;
    @include mixins.boxFlex();

    @include mixins.media('tablet-l') {
      margin-bottom: $margin-customer;
    }

    .icon-close {
      width: 11px;
      height: 11px;
    }
  }

  &__layout {
    padding: 0 16px;
    @include mixins.boxFlex(space-between, stretch, column);

    @include mixins.media('desktop') {
      padding: 0;
    }

    & > *:last-child {
      @include mixins.mediaCustom ($max-break-customer) {
        margin-bottom: $margin-customer;
      }
    }

    @include mixins.media('tablet-l') {
      flex-direction: row;
      flex-wrap: nowrap;

      & > *:first-child {
        margin-right: $margin-customer;
      }
    }

    @include mixins.media('desktop') {
      flex-direction: row;
      flex-wrap: nowrap;
    }
  }

  &__2-3 {
    width: 100%;

    @include mixins.media('tablet-l') {
      width: 80%;
    }
  }

  &__1-3 {
    display: none;
    width: 100%;

    @include mixins.media('tablet-l') {
      width: 20%;
    }
  }

  &__section-space {
    &:first-of-type {
      margin-bottom: $margin-customer;
    }
  }
}

.customer__wrapper--empty {
  width: 100%;
  height: 100%;
  padding: 16px;
  background-color: variables.$color-secondary;
  @include mixins.boxFlex(center, center, column);

  p {
    margin-bottom: variables.$margin-m;
    text-align: center;
    color: variables.$color-background-2;

    @include mixins.media("tablet") {
      margin-bottom: 48px;
    }
  }
}
