@use "../abstracts/variables";
@use "../abstracts/mixins";

.text-image {
  margin-top: 40px;
  margin-bottom: 24px;

  @include mixins.media('tablet') {
    margin-top: variables.$margin-section;
    margin-bottom: variables.$margin-section;
  }

  &__title.h1,
  &__title.h2 {
    font-size: variables.$font-size--mobile--h1-customer;
    line-height: variables.$line-height--mobile--h1-customer;

    @include mixins.media('tablet-l') {
      font-size: variables.$font-size--mobile-h1;
      line-height: variables.$line-height--mobile-h1;
    }
  }

  &__title, &__subtitle {
    text-align: center;
  }

  &__title {
    color: variables.$color-black;
  }

  &__image {
    width: 100%;
    padding-top: 43%;
    position: relative;

    @include mixins.media('tablet') {
      padding-top: 15.5%;
    }

    & > img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__subtitle {
    margin-top: variables.$margin-l;
    font-weight: variables.$font-regular;

    @include mixins.media('tablet') {
      margin-top: variables.$margin-section;
    }
  }

  &__description {
    margin-top: variables.$margin-l;
    text-align: justify;
    font-size: variables.$font-size--general-m;
    line-height: 21px;

    @media (max-width: 750px) {
      padding: 0;
    }
  }

  &__blockquote {
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: variables.$font-size--general-m;

    & > img {
      margin-bottom: 21px;
    }

    & > p {
      color: variables.$color-principal;
    }
  }

  &__link {
    margin: variables.$margin-l auto 0;
  }
}
