@use "../abstracts/variables";
@use "../abstracts/mixins";

a.customer__nav-link > svg {
  height: 11px;
  width: 11px;

  path {
    fill: variables.$color-principal;
  }
}

.customer__sections {
  width: 100%;
  margin: 0;

  .account__wishlist,
  .my-sales,
  .newsletter,
  .text-image,
  .customer__addresses,
  .accordion__section {
    width: 100%;
  }

  @include mixins.media("tablet-l") {
    width: 74.5%;
  }

  .btn-1:visited {
    color: variables.$color-white;
  }

  .shopify-section {
    height: 100%;
  }

  .accordion__section,
  .text-image {
    margin: 0;
    padding: 0;
  }
  .accordion-item__content {
    text-align: justify;
  }

  .accordion-item__button span {
    font-size: variables.$font-size--general-up-m;
    line-height: variables.$line-height--general-up-s;
  }

  .text-image {
    &__title,
    h3,
    &__description,
    &__image {
      margin: 0 0 variables.$margin-l;
    }

    &__link {
      width: 100%;

      @include mixins.media("phone-m") {
        width: fit-content;
      }
    }
  }

  .my-sales {
    height: 100%;

    .v-account-title,
    #customer-id {
      display: none;
    }

    .sell__wrapper {
      height: 100%;
      padding: 16px;
      background-color: variables.$color-secondary;
      @include mixins.boxFlex(center, center, column);

      p {
        margin-bottom: variables.$margin-m;
        color: variables.$color-background-2;

        @include mixins.media("tablet") {
          margin-bottom: 48px;
        }
      }
    }
  }

  .newsletter {
    padding: 0;
    background-color: variables.$color-white;

    form {
      width: 100%;
      order: -1;

      .field__input {
        color: variables.$color-black;
      }
    }

    .newsletter__title {
      margin-bottom: variables.$margin-l;
      font-family: variables.$font-family--general;
      font-weight: variables.$font-light;
      font-size: variables.$font-size--general-up-m;
      line-height: variables.$line-height--general-up-s;
      order: -2;
      color: variables.$color-principal;
    }

    .newsletter__text {
      margin-top: variables.$margin-l;
      margin-bottom: 0;
    }

    .newsletter__container {
      @include mixins.boxFlex(center, center, column);
    }

    .newsletter__text {
      color: variables.$color-black;
    }

    .wrapper-xl {
      padding-left: 0;
      padding-right: 0;

      @include mixins.media("tablet-l") {
        padding-left: 116px;
        padding-right: 116px;
      }
    }

    .letter-icon {
      display: none;
    }

    .newsletter-form .newsletter-form__input-wrapper input[type="email"] {
      padding: 20px 12px;
      border-bottom: 1px solid variables.$color-principal;
    }

    .newsletter-form__message {
      &--success {
        color: variables.$color-newsletter-success;
      }
      &--error {
        color: variables.$color-newsletter-error;
      }
    }

    #NewsletterForm--newsletter {
      border-bottom: 1px solid variables.$color-principal;
    }

    #Subscribe {
      color: variables.$color-white;
      background-color: variables.$color-principal;
    }

    .unsubscribe__button {
      margin: variables.$margin-l auto 0;
    }
  }
}

footer #shopify-section-newsletter {
  .unsubscribe__button {
    display: none;
  }
}

.customer {
  .account-sidebar {
    background-color: variables.$color-secondary;
  }

  .account-sidebar a,
  .account-sidebar a:visited {
    color: variables.$color-black;
  }

  .account-sidebar a {
    transition: text-shadow 0.3s ease-in-out;

    &:hover {
      text-shadow: -0.4px -0.4px 0 variables.$color-black, 0.4px 0.4px variables.$color-black;
    }
  }

  .account-sidebar .active a,
  .account-sidebar__mobile .custom-select__selected {
    color: variables.$color-principal !important;
  }

  .account-sidebar .active a {
    transition: text-shadow 0.3s ease-in-out;
    text-shadow: -0.4px -0.4px 0 variables.$color-principal, 0.4px 0.4px variables.$color-principal;
  }
}
