@use '../abstracts/variables';
@use '../abstracts/mixins';

$margin-adresses: 30px;

.address {
  &__btn-new,
  &__edit {
    &[data-active="false"] {
      display: none;
    }
  }

  .address__cancel {
    margin-top: variables.$margin-l;
  }

  &__btn-new, .address__cancel {
    width: 100%;
  }

  .modify-btn, &__btn-new {
    font-size: variables.$font-size--general-m;
  }

  &__btn-new {
    @include mixins.media('tablet-l') {
      width: auto;
      margin-left: auto;
    }
  }

  &__title {
    display: block;
    margin-bottom: variables.$margin-l;
    font-weight: variables.$font-regular;
  }

  &__edit {
    @include mixins.media('tablet-l') {
      width: 436px;
      margin: 0 auto;
    }
  }

  &__actions {
    --columns: 1fr 1fr;
    margin-top: auto;

    form button {
      width: 100%;
    }
  }

  &__form {
    --columns: 1fr 1fr;

    &-100 {
      grid-column: 1 / span 2;
    }

    input, input::placeholder {
      color: variables.$color-background-2;
    }
  }

  &__detail {
    text-align: center;
    margin-bottom: 16px;

    p {
      line-height: variables.$line-height--general-m;
    }
  }

  &__detail-badge {
    padding: 4px 8px;
    font-size: variables.$font-size--general-s;
    background-color: variables.$color-secondary;
    color: variables.$color-accent-3;
  }

  &__list {
    margin-top: variables.$margin-l;
    list-style: none;

    @include mixins.boxGrid(auto, auto, 24px, 24px);

    @include mixins.media('tablet-l') {
      @include mixins.boxGrid(49% auto, auto, 24px, 24px);
    }

    li {
      padding: 16px;
      border: 1px solid variables.$color-principal;
      display: flex;
      flex-direction: column;
    }

    .address__edit {
      @include mixins.media('tablet-l') {
        order: 1;
        grid-column: 1 / 3;
      }

      input {
        color: variables.$color-black;
      }
    }
  }

  #AddressCountryNew, .input-container input {
    padding: 9px 12px;
  }

  .default-wrapper, .default-checkbox {
    @include mixins.boxFlex(flex-start, center);

    label {
      margin-left: 10px;
    }
  }

  .default-checkbox {
    margin: 12px 0;
  }

  label[for="address_default_address_new"] {
    margin-left: 10px;
  }
}
