@use "../abstracts/variables";
@use "../abstracts/mixins";

$min-break-customer: "min-width: 800px";
$max-break-customer: "max-width: 800px";

.customer__orders {
  width: 100%;

  @include mixins.media("tablet-l") {
    width: 101%;
  }

  &:has(.customer__orders--empty) {
    width: 100%;
  }

  &-wrapper {
    margin-bottom: variables.$margin-l;

    &:has(.customer__orders--empty) {
      overflow: auto;
      width: 100%;
      margin-bottom: 0;
      padding-right: 0;
    }

    @include mixins.media("tablet-l") {
      padding-right: 8px;
      max-height: 325px;
      overflow-y: auto;
    }

    &::-webkit-scrollbar {
      width: 5px;
    }

    &::-webkit-scrollbar-track {
      box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 2px;
      background-color: variables.$color-background-2;
      outline: 1px solid variables.$color-background-2;
    }
  }

  .customer__wrapper--empty {
    width: 100%;
    height: 100%;
    padding: 16px;
    background-color: variables.$color-secondary;
    @include mixins.boxFlex(center, center, column);
  }

  &--empty p {
    margin-bottom: variables.$margin-m;
    text-align: center;
    color: variables.$color-background-2;

    @include mixins.media("tablet") {
      margin-bottom: 48px;
    }
  }
}

.account__btn--empty {
  width: 100% !important;
  height: 40px;
  margin: 0 auto;
  padding: 8px;
  font-size: variables.$font-size--general-m;

  &.btn-link.btn-2 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @include mixins.media("tablet") {
    width: fit-content !important;
    height: 52px;
    padding: 14px 11px;
  }
}

.customer__order {
  &-list {
    padding: 16px;
    border: 1px solid variables.$color-principal;
    flex-wrap: wrap;
    --row-gap: 12px;
    @include mixins.boxFlex(space-between, center, row);

    @include mixins.media("tablet-l") {
      display: grid;
      --columns: 1fr 1.3fr 2.5fr 0.8fr 1.2fr;
      --column-gap: 30px;
      padding: 20px 0;
      border: none;
      border-bottom: 1px solid variables.$color-background-2;
    }

    &:first-of-type {
      @include mixins.media("tablet-l") {
        padding-top: 10px;
      }
    }

    &:not(:last-of-type) {
      margin-bottom: variables.$margin-l;

      @include mixins.media("tablet-l") {
        margin-bottom: 0;
      }
    }

    .customer__badge {
      display: inline-block;
      order: -2;
      width: 100%;
      padding: 8px 0;
      text-align: center;
      font-weight: variables.$font-semibold;
      line-height: variables.$line-height--general-m;
      background-color: variables.$color-secondary;
      color: variables.$color-principal;

      @include mixins.media("tablet-l") {
        order: unset;
        width: auto;
        padding: 0;
        text-align: start;
        background-color: unset;
      }
    }

    .cart-items__container--product-info {
      @include mixins.media("tablet-l") {
        justify-content: flex-start;
      }
    }

    .cart-items__container--title {
      @include mixins.media("tablet-l") {
        font-size: 11px;
      }
    }

    .customer__badge:visited {
      color: variables.$color-principal;
    }

    .customer__price,
    .customer__date {
      order: -1;
      width: 40%;
      line-height: variables.$line-height--general-m;

      @include mixins.media("tablet-l") {
        order: unset;
        width: auto;
      }
    }

    .customer__price {
      text-align: end;

      @include mixins.media("tablet-l") {
        text-align: start;
      }

      span {
        width: 100%;
      }
    }

    .customer__details {
      width: 100%;
      font-size: variables.$font-size--general-up-s;

      @include mixins.media("tablet-l") {
        width: auto;
      }
    }

    .cart-items__container {
      grid-template-columns: 60px auto;
      margin: 0;

      @include mixins.media("tablet-l") {
        grid-template-columns: 44px auto;
      }

      &--product-title,
      &--image {
        width: 60px;

        @include mixins.media("tablet-l") {
          width: 44px;
        }
      }
      &--image {
        height: 60px;

        @include mixins.media("tablet-l") {
          height: 44px;
        }
      }
      &--vendor {
        font-size: variables.$font-size--general-l;
        line-height: variables.$line-height--general-l;

        @include mixins.media("tablet-l") {
          margin-top: 2px;
          margin-bottom: 3px;
          font-size: variables.$font-size--general-up-m;
          line-height: variables.$line-height--general-xs;
        }
      }
      &--title {
        font-size: variables.$font-size--general-s;
        line-height: variables.$line-height--general-up-s;
        @include mixins.excerptForLine(2);

        @include mixins.media("tablet-l") {
          font-size: variables.$font-size--general-m;
          line-height: variables.$line-height--general-up-s;
        }
      }

      &--title span {
        text-align: start;
      }
    }

    & > * {
      align-self: center;
      display: inline-flex;
    }

    .wk-button,
    &--header {
      display: none;
    }

    &--header {
      @include mixins.media("tablet-l") {
        display: grid;
        font-weight: variables.$font-light;
        padding-bottom: 8px;
        margin-bottom: 0;
      }
    }

    &--detail {
      .customer__badge,
      .customer__date {
        display: none;

        @include mixins.media("tablet-l") {
          display: block;
          opacity: 0;
        }
      }

      &:nth-of-type(2) {
        .customer__badge,
        .customer__date {
          display: block;
          opacity: 1;
        }
      }

      & > *:last-child {
        justify-content: center;
        text-align: right;
      }
    }

    & > div {
      @include mixins.mediaCustom($max-break-customer) {
        display: flex;
        justify-content: space-between;
      }

      & > *:last-child {
        @include mixins.mediaCustom($max-break-customer) {
          text-align: right;
        }
      }
    }

    @include mixins.mediaCustom($max-break-customer) {
      --columns: 1fr;
    }
  }

  &-total {
    display: flex;
    justify-content: space-between;

    &:not(:last-of-type) {
      margin-bottom: 15px;
    }

    & > *:first-child {
      display: block;
      margin-right: $margin-customer;
    }

    & > *:last-child {
      text-align: right;
    }

    .discount-wrapper {
      margin-top: 3px;

      @include mixins.media("tablet-l") {
        margin-top: 0;
        margin-left: 5px;
        display: inline-block;
      }
    }

    .discount-name {
      color: variables.$color-principal;
    }
  }

  &-date {
    margin-bottom: calc($margin-customer / 2);
  }
}

.customer__refund,
.customer__return {
  width: 100% !important;

  @include mixins.media("phone-m") {
    width: fit-content !important;
  }
}

.customer__refund {
  color: variables.$color-white !important;

  &:hover {
    color: variables.$color-principal !important;
  }
}

.customer__return {
  margin-bottom: variables.$margin-l;
  margin-left: auto;
  padding: 9px;

  .icon-caret {
    order: -1;
    width: 11px;
    transform: rotate(90deg);
  }
}

.customer__individual {
  .customer__layout {
    width: 100%;

    @include mixins.media("tablet-l") {
      padding: 0;
    }
  }

  .customer__order-list--header {
    padding: 0 0 8px;
  }

  .customer__detail {
    margin: 0;
    @include mixins.media("tablet-l") {
      width: 100%;
    }
  }

  .customer__order {
    @include mixins.media("tablet-l") {
      width: 99%;
    }
  }

  .customer__order-list {
    margin: 0;
    padding: 0 0 16px;
    --columns: 12.02% 14.92% 29.36% 15.17% 8.38% 8.71%;

    @include mixins.media("tablet-l") {
      --column-gap: 8px;
    }
    @include mixins.media("desktop") {
      --column-gap: 15px;
    }

    &:not(.customer__order-list--header) {
      border: none;
      margin: 0 0 16px;
      padding: 0 0 16px;
      border-bottom: 1px solid variables.$color-secondary;

      @include mixins.media("tablet-l") {
        padding: 16px 0;
        border-bottom: none;
      }
    }
  }

  .ref__hidden {
    @include mixins.media("tablet-l") {
      display: none;
    }
  }

  .customer__info-details,
  .customer__qty,
  .customer__date {
    width: 100%;

    @include mixins.media("tablet-l") {
      width: auto;
    }
  }

  .customer__info-details:nth-last-child(-n + 2) {
    @include mixins.media("tablet-l") {
      justify-content: flex-end;
    }
  }

  .customer__date {
    width: 100%;
    border-bottom: 1px solid variables.$color-secondary;
    padding-bottom: 8px;

    @include mixins.media("tablet-l") {
      width: auto;
      border-bottom: none;
      padding-bottom: 0;
    }
  }
  .customer__qty {
    @include mixins.media("tablet-l") {
      justify-content: center;
    }
  }

  .customer__order-total:first-child {
    padding: 16px 0 0;
    background-color: red;
  }

  .customer__order-total:last-of-type {
    span:first-child {
      color: variables.$color-principal;
      font-size: variables.$font-size--general-l;

      @include mixins.media("tablet-l") {
        text-transform: uppercase;
      }
    }
  }

  .customer__order-address {
    margin-top: variables.$margin-l;

    @include mixins.boxGrid(auto, auto auto, 0, 24px);

    @include mixins.media("tablet-l") {
      @include mixins.boxGrid(auto auto, auto, 24px);
    }
  }

  .customer__order-space {
    border: 1px solid variables.$color-principal;
    padding: 16px;
  }

  .customer__order-status {
    text-transform: uppercase;
    margin: 10px 0;
  }

  .customer__tracking {
    width: 100%;
    margin: variables.$margin-m 0 0 auto;
    text-align: center;

    @include mixins.media("tablet-l") {
      width: fit-content;
    }

    span {
      font-size: variables.$font-size--general-up-s !important;
      color: inherit !important;
    }
  }

  .h5 {
    font-weight: variables.$font-regular;
  }

  .cart-items__container {
    width: 100%;
    flex-direction: row-reverse;

    @include mixins.media("tablet-l") {
      flex-direction: row;
      margin: 0 0 4px;
    }
  }
}
