@use "../abstracts/variables";
@use "../abstracts/mixins";

.account__wishlist {
  &-title {
    margin-top: 40px;
    margin-bottom: variables.$margin-m;

    @include mixins.media("tablet-l") {
      margin-top: variables.$margin-section;
      margin-bottom: variables.$margin-l;
    }
  }

  &-title,
  .wk-note__login {
    text-align: center;
  }
}

.wishlist {
  margin: 0 0 72px;

  @include mixins.media("phone-m") {
    margin: 0 auto 72px;
  }
  @include mixins.media("desktop") {
    min-width: 1080px;
    margin: 0 auto 87px;
  }
  @include mixins.media("desktop-m") {
    min-width: 1440px;
  }

  .wk-note__login {
    width: fit-content;
    margin: 0 auto 24px;
    padding: 16px;
    background-color: variables.$color-secondary;

    @include mixins.media("tablet-l") {
      margin: 0 auto 48px;
      font-size: variables.$font-size--general-l;
    }

    a:visited {
      color: variables.$color-principal;
    }
  }

  .carousel-products__slide-btn {
    margin-bottom: 46px;
    padding-bottom: 46px;
    border-bottom: 1px solid variables.$color-background;

    @include mixins.media("tablet") {
      padding-bottom: 0;
      border-bottom: none;
    }
    @include mixins.media("desktop") {
      margin-bottom: variables.$margin-section;
      padding-bottom: 46px;
    }
  }

  .swiper-slide {
    max-width: 252px;
    height: auto;
  }

  .wk-button__icon {
    width: 15px !important;
    height: 15px !important;
  }
}

.wishlist__recommendation {
  max-width: calc(100vw - 55px);

  @include mixins.media("tablet-l") {
    max-width: calc(100vw - 380px);
  }

  .carousel-products__slide-btn {
    padding: 0;
    margin-bottom: 30px;
  }

  .carousel-products__slide-btn .btn-2.account__btn--empty {
    display: none;
  }

  .btn-link {
    width: 100%;

    @include mixins.media("phone-m") {
      width: fit-content;
    }
  }

  .subtitle {
    text-align: center;
  }
}

.customer__layout {
  .wishlist .carousel-products__slide-btn {
    padding-bottom: 0;
  }
  .wishlist:has(.wk-page--empty) {
    width: 100%;
    height: 100%;
    padding: 16px;
    background-color: variables.$color-secondary;
    @include mixins.boxFlex(center, center, column);

    .wishlist__recommendation {
      display: block;
      width: 100%;
    }


    p {
      color: variables.$color-background-2;
    }
  }
}

.wishlist:has(.wk-page--empty) {
  .wishlist__recommendation {
    display: block;

    .carousel-products__slide-btn .btn-2.account__btn--empty {
      display: block;
      height: auto;
    }
  }
}

.wk-page {
  .wk-button--remove {
    z-index: 2 !important;
  }

  .account-wishlist-grid {
    @include mixins.boxGrid(auto, auto, 24px, 24px);

    @include mixins.boxGrid(auto, auto, 24px, 24px);

    @include mixins.media("phone") {
      @include mixins.boxGrid(46% 46%, auto, 24px, 24px);
    }
    @include mixins.media("phone-m") {
      @include mixins.boxGrid(47% 47%, auto, 24px, 24px);
    }
    @include mixins.mediaCustom("min-width: 540px") {
      @include mixins.boxGrid(47.5% 47.5%, auto, 24px, 24px);
    }
    @include mixins.mediaCustom("min-width: 640px") {
      @include mixins.boxGrid(48% 48%, auto, 24px, 24px);
    }
    @include mixins.media("tablet") {
      @include mixins.boxGrid(48.25% 48.25%, auto, 24px, 24px);
    }
    @include mixins.media("tablet-l") {
      @include mixins.boxGrid(repeat(3, 31%), auto, 24px, 24px);
    }
    @include mixins.media("desktop") {
      @include mixins.boxGrid(repeat(3, 23.3%) auto, auto, 24px, 24px);
    }
  }

  .btn-1 {
    width: 100%;
  }

  .product-card__image-2 {
    opacity: 0;
  }

  .product-card.out-of-stock {
    opacity: .5;
  }

  .wk-note__list-empty {
    text-align: center;
  }
}

.account__wishlist-reference .wk-page {
  .account-wishlist-grid {
    @include mixins.media("desktop") {
      @include mixins.boxGrid(repeat(3, 23.3%) auto, auto, 24px, 24px);
    }
  }
}

.customer .wk-page  {
  .btn-1 {
    width: 100%;
  }

  .wk-note__login {
    display: none;
  }
}

.customer .wishlist {
  min-width: unset;
  margin-bottom: 0;
  padding-left: 0;
  padding-right: 0;
}

.overlay {
  z-index: 4 !important;
}


.wk-page .product-card:first-of-type {
  .product-card--head-images {
    &[data-load="false"] img {
      opacity: 0;
    }
  }
}
